import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import MetricCVS from '../views/MetricCVS.vue'
// import BaseInfoEditor from '../views/BaseInfoEditor.vue'
// import EducationLevelEditor from '../views/EducationLevelEditor.vue'
// import InternshipExperEditor from '../views/InternshipExperEditor.vue'
// import ClubOrgExperEditor from '../views/ClubOrgExperEditor.vue'
// import SkillsCertOtherEditor from '../views/SkillsCertOtherEditor.vue'

import testInd from '../views/testInd.vue'
import testEd from '../views/testEd.vue'

Vue.use(VueRouter)


const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
	{
		path: "/",
		redirect: "/metric-cvs"
	},
	{
		path: "/metric-cvs",
		name: "简历生成器",
		component:MetricCVS,
	},
	{
		path: "/test",
		name: "测试页",
		component: testInd,
		children: [
			{
				path: "/testEd",
				name: "测试页子页",
				component: testEd
				// props: { default: true, sidebar: false }
			}
		]
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
